.home-content {
    display: flex;
    flex-direction: column;
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 500px 200px 60px 1fr;
    gap: 15px;
    grid-template-areas: 
        "major major major"
        "cetus fortuna deimos"
        "explore-title explore-title explore-title"
        "primary secondary warframe";
}

.news {
    background: var(--sec-bg-color)
}

.major-news {
    grid-area: major;
    width: 100vw;
    height: 100%;
    justify-self: center;
}

.major-news img {
    object-fit: cover;
}

.explore-title {
    grid-area: explore-title;
    font-family: 'Orbitron', sans-serif;
    font-size: 1.8rem;
    color: white;
}